@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;600;700&display=swap);
html{
    overflow-x: hidden;
}
body{
    font-family: 'Poppins', sans-serif;
    font-size: 13px;
    font-weight: 500;
    padding:0;
    margin:0;
}
#welcome{
    width: 100%;
    height: 480px;
    position: relative;
}
.details{
    width: 45%;
    height: 85%;
    position: absolute;
    top: 50%;
    left: 10%;
    transform: translateY(-50%);
}
.details h1{
    font-size: calc(4.2em + 1vw);
    line-height: 1.2;
    margin: 0;
    position: absolute;
    top: 12%;
}
.details p{
    font-size: 1.3em;
    line-height: 1.7;
    padding-right: 20%;
    color: hsl(0, 0%, 55%);
    position: absolute;
    top: 50%;
}
.details button{
    font-size: 1.3em;
    font-weight: 700;
    padding: 15px 40px;
    border-radius: 30px;
    border-width: 0;
    background-color: hsl(180, 66%, 49%);
    color: #FFF;
    position: absolute;
    top: 75%;
}
.details button:hover{
    background-color: hsl(180, 66%, 70%);
}
#welcome img{
    height: 87%;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(10%,-50%);
}

.mainDiv{
    width: 100%;
    background-color: hsl(0, 0%, 95%);
    position: relative;
    top: 100px;
}
.inputDiv{
    width: 80%;
    height: 150px;
    border-radius: 10px;
    background-image: url(/URL-shortening-api-master/static/media/bg-shorten-desktop.0d9742f4.svg);
    background-color: hsl(257, 27%, 26%);
    position: relative;
    left: 50%;
    transform: translate(-50%,-50%);
}
.inputDiv input{
    font-size: 1.2em;
    font-weight: 500;
    letter-spacing: 0.5px;
    width: 73%;
    height: 35%;
    padding-left: 30px;
    box-sizing: border-box;
    border-width: 0;
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 5%;
    transform: translateY(-50%);
}
input:focus{
    outline: none;
}
input:-ms-input-placeholder{
    position: absolute;
    top: 50%;
    left: 30px;
    transform: translateY(-50%);
}
input::placeholder{
    position: absolute;
    top: 50%;
    left: 30px;
    transform: translateY(-50%);
}
.inputDiv span{
    display: none;
    font-size: 1em;
    font-weight: 500;
    color: red;
    padding-left: 30px;
    position: absolute;
    top: 70%;
    left: 30px;
}
.inputDiv button{
    font-size: 1.2em;
    font-weight: 600;
    letter-spacing: 0.5px;
    width: 15%;
    height: 35%;
    border-width: 0;
    border-radius: 10px;
    background-color: hsl(180, 66%, 49%);
    color: #FFF;
    position: absolute;
    top: 50%;
    right: 5%;
    transform: translateY(-50%);
}
.inputDiv button:hover{
    background-color: hsl(180, 66%, 70%);
}
.linkContainer{
    width: 80%;
    position: relative;
    left: 50%;
    transform: translate(-50%,-50px);
}
.linkContainer ul{
    width: 100%;
    list-style-type: none;
    padding: 0;
    margin: 0;
}
.listItem{
    max-width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    white-space: pre;
    border-radius: 5px;
    background-color: #FFF;
    margin-bottom: 15px;
    position: relative;
}
.listItem hr{
    display: none;
}
.originalLink{
    font-size: 1.1em;
    position: absolute;
    left: 30px;
}
.newLink{
    font-size: 1.1em;
    color: hsl(180, 66%, 49%);
    position: absolute;
    right: 135px;
}
.copyLink{
    font-size: 1em;
    padding: 10px 20px;
    border-radius: 3px;
    border-width: 0;
    background-color: hsl(180, 66%, 49%);
    color: #FFF;
    position: absolute;
    right: 30px;
}
.copyLink:hover{
    background-color: hsl(180, 66%, 70%);
}
.clearHistory{
    font-size: 1em;
    padding: 10px 40px;
    margin-top: 10px;
    border-radius: 3px;
    border-width: 0;
    background-color: hsl(180, 66%, 49%);
    color: #FFF;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
.clearHistory:hover{
    background-color: hsl(180, 66%, 70%);
}

.moreInfo{
    width: 40%;
    height: 150px;
    text-align: center;
    position: relative;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
}
.moreInfo h2{
    width: 100%;
    font-size: 2.5em;
    position: absolute;
    top: 5%;
}
.moreInfo p{
    font-size: 1.1em;
    line-height: 2;
    padding: 0 5%;
    color: hsl(0, 0%, 55%);
    position: absolute;
    top: 50%;
}
.features{
    width: 80%;
    position: relative;
    top: 100px;
    left: 50%;
    transform: translateX(-50%);
}
.features #horizontalLine{
    display: block;
    width: 50%;
    border-top: 5px solid hsl(180, 66%, 49%);
    border-bottom: 5px solid hsl(180, 66%, 49%);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 1;
}
.features #verticalLine{
    display: none;
}
.featureContainer{
    height: 250px;
    display: flex;
    justify-content: space-between;
    position: relative;
}
.item{
    width: 31%;
    height: 200px;
    border-radius: 5px;
    background-color: #FFF;
    z-index: 2;
}
.imgBorder{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: hsl(260, 8%, 14%);
    position: relative;
    left: 10%;
    transform: translateY(-50%);
}
.featureContainer img{
    width: 35px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.item h3{
    width: 80%;
    font-size: 1.3em;
    padding: 0 10%;
    position: relative;
    top: -15%;
}
.item p{
    width: 80%;
    font-size: 1em;
    line-height: 1.7;
    padding: 0 10%;
    color: hsl(0, 0%, 55%);
    position: relative;
    top: -18%;
}
.item-recognition{
    align-self: start;
}
.item-records{
    align-self: center;
}
.item-custom{
    align-self: end;
}

.bottom{
    width: 100%;
    height: 230px;
    position: relative;
    top: 200px;
    background-image: url(/URL-shortening-api-master/static/media/bg-boost-desktop.772154c9.svg);
    background-color: hsl(257, 27%, 26%);
}
.bottom span{
    width: 100%;
    font-size: 2.5em;
    font-weight: 700;
    text-align: center;
    color: #FFF;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translateX(-50%);
}
.bottom button{
    font-size: 1.3em;
    font-weight: 700;
    padding: 15px 40px;
    border-radius: 30px;
    border-width: 0;
    background-color: hsl(180, 66%, 49%);
    color: #FFF;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translateX(-50%);
}
.bottom button:hover{
    background-color: hsl(180, 66%, 70%);
}

@media screen and (max-width: 770px){
    #welcome{
        height: 650px;
    }
    .details{ 
        width: 100%;
        height: 47%;
        top: 53%;
        left: 50%;
        transform: translate(-50%,0);
    }
    .details h1{
        width: 95%;
        font-size: calc(2.8em + 1vw);
        text-align: center;
        top: 0%;
        left: 50%;
        transform: translateX(-50%);
    }
    .details p{
        width: 90%;
        text-align: center;
        padding-right: 0;
        top: 32%;
        left: 50%;
        transform: translateX(-50%);
    }
    .details button{
        white-space: nowrap;
        top: 75%;
        left: 50%;
        transform: translateX(-50%);
    }
    #welcome img{
        height: 45%;
        top: 0%;
        right: 50%;
        transform: translate(65%,0);
    }
    .mainDiv{
        top: 100px;
    }
    .inputDiv{
        width: 85%;
        height: 180px;
        background-image: url(/URL-shortening-api-master/static/media/bg-shorten-mobile.41c34b33.svg);
        background-repeat: no-repeat;
    }
    .inputDiv input{
        width: 85%;
        height: 30%;
        font-size: 1.3em;
        padding-left: 30px;
        border-radius: 5px;
        top: 11%;
        left: 50%;
        transform: translate(-50%,0);
    }
    .inputDiv span{
        font-size: 1.2em;
        top: 42%;
    }
    .inputDiv button{
        width: 85%;
        height: 30%;
        font-size: 1.3em;
        border-radius: 5px;
        top: 89%;
        right: 50%;
        transform: translate(50%,-100%);
    }
    .linkContainer{
        width: 85%;
        transform: translate(-50%,-70px);
    }
    .listItem{
        height: 150px;
        align-items: start;
        margin-bottom: 20px;
        background-color: #FFF;
    }
    .listItem hr{
        display: block;
        width: 100%;
        border-bottom: 1px solid black;
        opacity: 0.1;
        position: absolute;
        top: 25%;
    }
    .originalLink{
        font-size: 1.3em;
        position: relative;
        top: 7%;
        left: 7%;
    }
    .newLink{
        font-size: 1.3em;
        position: absolute;
        top: 36%;
        left: 7%;
    }
    .copyLink{
        width: 90%;
        height: 40px;
        font-size: 1.2em;
        font-weight: 700;
        padding: 0;
        top: 62%;
        left: 50%;
        transform: translateX(-50%);
    }
    .clearHistory{
        width: 50%;
        height: 40px;
        font-size: 1.2em;
        font-weight: 700;
        padding: 0;
    }
    .moreInfo{
        width: 85%;
        height: 150px;
        top: 10px;
    }
    .moreInfo h2{
        font-size: 2em;
        top: 5%;
    }
    .moreInfo p{
        font-size: 1.2em;
        line-height: 2;
        padding: 0;
        top: 45%;
    }
    .features{
        width: 85%;
        height: 850px;
        top: 135px;
    }
    .features horizontalLine{
        display: none;
    }
    .features #verticalLine{
        display: block;
        height: 90%;
        border: 5px solid hsl(180, 66%, 49%);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        z-index: 1;
    }
    .featureContainer{
        flex-direction: column;
        justify-content: start;
        grid-gap: 100px;
        gap: 100px;
    }
    .item{
        width: 100%;
        height: 250px;
        align-self: center;
        position: relative;
    }
    .imgBorder{
        width: 80px;
        height: 80px;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    .item h3{
        width: 100%;
        font-size: 1.5em;
        padding: 0;
        text-align: center;
        top: -15%;
    }
    .item p{
        font-size: 1.1em;
        text-align: center;
        top: -17%;
    }
    .item-recognition{
        left: 0;
    }
    .item-records{
        left: 0;
    }
    .item-custom{
        left: 0;
    }
    .bottom{
        height: 280px;
        top: 280px;
        background-image: url(/URL-shortening-api-master/static/media/bg-boost-mobile.4b04bd89.svg);
    }
    .bottom span{
        width: 100%;
        font-size: 2em;
        top: 32%;
    }
    .bottom button{
        white-space: nowrap;
        padding: 20px 50px;
        top: 55%;
    }
}
header{
    width: 100%;
    height: 120px;
    position: relative;
}
#logo{
    width: 100px;
    position: absolute;
    top: 50%;
    left: 10%;
    transform: translateY(-50%);
}
nav{
    font-weight: 600;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 17%;
    transform: translateY(-50%);
}
nav li{
    display: inline;
    color: hsl(0, 0%, 55%);
    padding: 0 15px;
    position: relative;
}
nav a:first-child{
    position: relative;
    left: 0;
}
nav li a:hover{
    color: hsl(260, 8%, 14%);
}
.menu hr{
    display: none;
    border: 1px solid white;
    opacity: 0.1;
    position: relative;
    top: 57%;
}
header a{
    font-weight: 600;
    text-decoration: none;
    color: hsl(0, 0%, 55%);
}
#login{
    position: absolute;
    top: 50%;
    right: 20%;
    transform: translateY(-50%);
}
header a:hover{
    color: hsl(260, 8%, 14%);
}
#signLink{
    font-weight: 700;
    padding: 10px 15px;
    border-radius: 20px;
    border-width: 0px;
    background-color: hsl(180, 66%, 49%);
    color: #FFF;
    position: absolute;
    top: 50%;
    right: 10%;
    transform: translateY(-50%);
}
#signLink:hover{
    background-color: hsl(180, 66%, 70%);
}
header svg{
    display: none;
    position: absolute;
    top: 50%;
    right: 7%;
    transform: translateY(-50%);
}

@media screen and (max-width: 770px){
    header{
        height: 110px;
        
    }
    #logo{
        width: 110px;
        left: 7%;
    }
    header svg{
        display: block;
    }
    .menu{
        visibility: hidden;
        opacity: 0;
        width: 90%;
        height: 0;
        border-radius: 5px;
        background-color: hsl(255, 11%, 22%);
        transition: visibility 0.2s ease;
        transition: height 0.3s ease;
        position: absolute;
        top: 75%;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
    }
    nav{
        width: 100%;
        font-weight: 600;
        opacity: 0;
        transition: opacity 0.3s ease;
        top: 5%;
        left: 50%;
        transform: translate(-50%,0);
    }
    nav ul{
        width: 100%;
        font-size: 1.5em;
        padding: 0;
        margin: 0;
    }
    nav li{
        display: block;
        padding: 20px 0;
        text-align: center;
    }
    nav li a{
        color: #FFF;
    }
    .menu hr{
        display: block;
    }
    #login{
        font-size: 1.5em;
        color: #FFF;
        opacity: 0;
        transition: opacity 0.3s ease;
        top: 65%;
        right: 50%;
        transform: translate(50%,0);
    }
    #signLink{
        font-size: 1.5em;
        padding: 15px 30%;
        border-radius: 25px;
        white-space: nowrap;
        opacity: 0;
        transition: opacity 0.3s ease;
        top: 78%;
        right: 50%;
        transform: translate(50%,0);
    }   
}
footer{
    width: 100%;
    height: 280px;
    background-color: hsl(260, 8%, 14%);
    position: relative;
    top: 300px;
}
footer svg{
    position: absolute;
    top: 20%;
    left: 10%;
}
footer svg path{
    fill: #FFF;
}
footer div{
    width: 10%;
    height: 100px;
    color: #FFF;
    position: absolute;
    top: 20%;
}
footer span{
    font-weight: 700;
}
footer ul{
    padding: 0;
    list-style-type: none;
    line-height: 2.2;
}
footer li a{
    text-decoration: none;
    color: hsl(0, 0%, 75%);
}
.list-features{
    position: absolute;
    left: 38%;
}
.list-resources{
    position: absolute;
    left: 51%;
}
.list-company{
    position: absolute;
    left: 64%;
}
li a:hover{
    color: hsl(180, 66%, 49%);
}
.icons{
    padding: 0;
    position: absolute;
    top: 20%;
    left: 75%;
}
.icons ul{
    list-style-type: none;
    white-space: nowrap;
    margin: 0;
}
.icons li{
    display: inline-block;
    height: 20px;
    padding: 0 20px;
    position: relative;
}
.icons path:hover{
    fill: hsl(180, 66%, 49%);
}

@media screen and (max-width: 770px){
    footer{
        height: 850px;
        top: 380px;
    }
    footer svg{
        top: 8%;
        left: 50%;
        transform: translateX(-50%);
    }
    footer div{
        width: 50%;
        text-align: center;
        font-size: 1.2em;
    }
    .list-features{
        position: relative;
        top: 18%;
        left: 50%;
        transform: translateX(-50%);
    }
    .list-resources{
        position: relative;
        top: 28%;
        left: 50%;
        transform: translateX(-50%);
    }
    .list-company{
        position: relative;
        top: 38%;
        left: 50%;
        transform: translateX(-50%);
    }
    .icons{
        height: 40px;
        top: 90%;
        left: 50%;
        transform: translateX(-50%);
    }
    .icons img{
        height: 10%;
    }
}
